.contact {
    margin-left: 130px;
    margin-bottom: 60px;
    padding-top: 50px;
}

.card0 {
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 0px;
}

.card2 {
    margin: 0px 40px;
}

.card1 .image {
    padding: 30px;
    margin-top: 30px;
    height: 100%;
    width: 100%;
}

.border-line {
    border-right: 1px solid #e0e0e0;
}

.line {
    height: 2px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
}

.or {
    display: flex;
    width: 10%;
    font-weight: bold;
}

::placeholder {
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
}

input,
textarea {
    padding: 10px 12px;
    border: 1px solid lightgray;
    border-radius: 2px;
    margin-top: 5px;
    margin-top: 2px;
    width: 100%;
    color: #138781;
    font-size: 1rem;
    letter-spacing: 1px;
}

input:focus,
textarea:focus {
    box-shadow: none !important;
    outline: 1px solid #138781 !important;
    outline-width: 0px;
}

button:focus {
    box-shadow: none;
    outline-width: 0px;
}

.button {
    background-color: #1e1e2c;
    color: white;
    border-radius: 2px;
    margin-top: 15px;
    padding: 10px;
}

.button:hover {
    background-color: #000;
    color: white;
}

.dont {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.okaynow {
    padding: 12px;
}

.dont h5 {
    margin-left: 8px;
}

/* ======================== */
@media (max-width: 980px) {
    .contact {
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }

    .contact .card2 {
        margin: 0;
    }

    .contact .card2 h6 {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media screen and (max-width: 1200px) {
    .dont h5 {
        margin-left: 6px;
        font-size: 18px;
    }

    .dont h4 {
        font-size: 20px;
    }

    .contact {
        margin: 0;
        padding: 0;
        margin-top: 50px;
    }
}


@media screen and (max-width: 830px) {
    .dont h5 {
        margin-left: 4px;
        font-size: 16px;
    }

    .dont h4 {
        font-size: 18px;
    }

}

@media screen and (max-width: 768px) {
    .dont h5 {
        margin-left: 4px;
        font-size: 20px;
    }

    .dont h4 {
        font-size: 24px;
    }

}

@media screen and (max-width: 580px) {
    .dont h5 {
        margin-left: 4px;
        font-size: 18px;
    }

    .dont h4 {
        font-size: 22px;
    }

    .dont {
        display: flex;
        flex-direction: column;
        align-items: normal;
    }
}